@charset "UTF-8";
    /* Texts */
    .mill_title {
        position: absolute;
        overflow: hidden;
        white-space: nowrap;
        top: 805px;
        left: 910px;
        font-size: 18px;
        color: #14cc00;
    }

    .mill_milepost {
