@charset "UTF-8";
    /* Texts */
    .bc_title {
        position: absolute;
        overflow: hidden;
        white-space: nowrap;
        top: 180px;
        left: 1008px;
        font-size: 18px;
        color: #14cc00;
    }

    .bc_milepost {
        position: absolute;
        overflow: hidden;
        white-space: nowrap;
        top: 200px;
        left: 1000px;
        font-size: 14px;
        color: white;
    }
    /* END Text */

    /* Switches */
    .bc_SW_1 {
        position: absolute;
        z-index: 2;
        top: 230px;
        left: 1018px;
        height: 38px;
        width: 38px;
        cursor: pointer;
    }
    /* END Switches*/

    /* Tail Tracks */
    .bc_1_west {
