        background: #999999;
    }

    .bc_east {
        position: absolute;
        top: 261px;
        left: 1056px;
        height: 7px;
        width: 25px;
        background: #999999;
    }
    /* END Tail Tracks */

    /* Signals */
    .bc_sig_2e {
        position: absolute;
        top: 235px;
        left: 995px;
        height:8px;
        width: 19px;
        cursor: pointer;
    }

    .bc_sig_4e {
        position: absolute;
        top: 266px;
