@charset "UTF-8";
    /* Texts */
    .sparrow_title {
        position: absolute;
        overflow: hidden;
        white-space: nowrap;
        top: 145px;
        left: 120px;
        font-size: 18px;
        color: #14cc00;
    }

    .sparrow_milepost {
        position: absolute;
        overflow: hidden;
        white-space: nowrap;
        top: 165px;
        left: 145px;
        font-size: 14px;
        color: white;
    }
    /* END Text */

    /* Switches */
    .sparrow_SW_1 {
        position: absolute;
        z-index: 2;
        top: 199px;
        left: 173px;
        height: 38px;
        width: 38px;
        cursor: pointer;
    }

    .sparrow_SW_3 {
